@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    margin: 0;
    background-color: rgb(234, 237, 237);
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}


.header {
    @apply h-[60px] flex items-center bg-[#131921] sticky top-0 z-10;
}

.complete-logo {
    text-decoration: none;
}

.header-logo {
    @apply flex items-center text-[#ff9f00] ml-6 mr-6;
}

.header-logo-image {
    @apply mr-3;
}

.header-logo-title {
    @apply border-0 text-white font-semibold text-lg;
}

.header-search {
    @apply flex flex-1 items-center rounded-3xl mr-3 ml-3;
}

.header-searchInput {
    @apply h-4 p-3 border-none w-full;
}

.header-searchIcon {
    @apply p-1 h-5 bg-[#ff9f00];
}

.header-nav {
    @apply flex justify-evenly;
}

.nav-item {
    @apply flex flex-col ml-3 mr-3 text-white;
}

.nav-item1 {
    @apply text-xs;
}

.nav-item2 {
    @apply text-sm font-extrabold;
}

.nav-basketCount {
    @apply mr-12 ml-1;
}

.itemBasket {
    @apply flex items-center text-white mr-4 ml-3;
}

.home {
    @apply flex justify-center ml-auto mr-auto max-w-[1500px] mb-16;
}

.home-image {
    -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
            mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    @apply w-full h-[400px] z-[-1] -mb-12;
}

.home-row {
    @apply flex z-[1] ml-1 mr-1 gap-5;
}

.home-container {
    @apply flex flex-col items-center;
}

.product {
    @apply flex flex-col items-center justify-end m-[10px] p-5 w-full max-h-[400px] min-w-[100px] max-w-full bg-white z-[1];
}

.add-to-basket-btn {
    @apply bg-[#ff9f00] border-solid border-[1px] mt-2 border-t-[#a88734] border-l-[#9c7e31] border-r-[#9c7e31] border-b-[#846a29] px-2 rounded-md py-1 text-amber-950 hover:text-[#111] hover:scale-105;
}

.product-image {
    @apply max-h-[200px] w-full object-contain mt-5 ml-4 mr-0 mb-0;
}

.product-info {
    @apply h-[100px] mb-4;
}

.checkout {
    @apply flex p-5 bg-white h-max;
}

.checkout-ad {
    @apply w-full mb-[10px];
}

.checkout-title {
    @apply mr-[10px] p-[10px] border-b-[#d3d3d3];
}

.checkout-left {
    @apply flex flex-col;
}

.checkoutProduct {
    @apply flex my-5;
}

.checkoutProduct-info {
    @apply pl-5;
}

.remove-btn {
    @apply bg-[#ff9f00] border border-solid mt-[10px] text-[#111];
    border-color: #a88734 #9c7e31 #846a29;
}

.checkoutProduct-image {
    @apply object-contain w-[180px] h-[180px];
}

.checkoutProduct-rating {
    @apply flex;
}

.checkoutProduct-title {
    @apply text-[17px] font-bold;
}

.subtotal {
    @apply flex flex-col justify-between w-[300px] min-h-[100px] p-5 bg-[#f3f3f3] border border-solid border-[#dddddd] rounded-[3px];
}

.subtotal-gift {
    @apply flex items-center mt-2 mb-3;
}

.subtotalInput {
    @apply mr-[5px] cursor-pointer;
}

.subtotalButton {
    @apply bg-[#ff9f00] rounded-sm w-full h-[30px] border border-solid mt-[10px] text-[#111];
    border-color: #a88734 #9c7e31 #846a29;
}

.login {
    @apply bg-white h-[100vh] flex flex-col items-center;
}

.login-logo {
    @apply flex my-[30px];
}

.login-logoImage {
    @apply text-[#ff9f00];
}

.login-logoTitle {
    @apply text-black;
}

.login-container {
    @apply w-[300px] h-fit flex flex-col rounded-[5px] border-[1px] border-solid border-[#lightgray] p-5;
}

.login-container > h1 {
    @apply font-medium mb-5;
}

.login-container > form > h5 {
    @apply mb-[5px];
}

.login-container > form > input {
    @apply h-[30px] mb-[10px] bg-white w-[98%] border-2 border-gray-300;
}

.login-container > p {
    @apply mt-[15px] text-[12px];
}

.login-signInButton {
    @apply bg-[#ff9f00] rounded-sm w-full h-[30px] border-[1px] border-solid mt-[10px];
    border-color: #a88734 #9c7e31 #846a29;
}

.login-registerButton {
    @apply rounded-sm w-full h-[30px] border-[1px] border-solid mt-[10px] border-[darkgray] bg-gray-200;
}

